<template>
  <div class="cms-list-layout">
    <cms-prompt :collapse='false'>
      <li>管理系统所使用的FTP服务器信息</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>FTP服务名称</label>
        <el-input v-model="queryParams.ftpName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/ftp/save'" icon="add" type="success" size="small" @click="$routerLink('/ftp/save','save')">新增</cms-button>
      <cms-button v-prem="'/ftp/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="ftpName" label="FTP服务名称" min-width='175'></el-table-column>
      <el-table-column prop="ip" label="ip" min-width="175"></el-table-column>
      <el-table-column prop="url" label="访问路径" min-width="195">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.url">{{scope.row.url}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="encoding" label="使用编码" min-width="155"></el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/ftp/update'" icon='edit' size='small' type='info' @click.stop="$routerLink('/ftp/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/ftp/delete'" icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  ftpList,
  ftpDelete
} from '@/api/setting/ftp.js'
var defaultData = {
  'ftpName': ''
}
export default {
  name: 'ftpList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(ftpDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(ftpDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(ftpList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(ftpList, this.queryParams)
  }
}
</script>

<style>
</style>



